
































































































import Vue from 'vue'

export default Vue.extend({
  name: 'upload-drag-and-drop',
  props: {
    asButton: {
      type: Boolean,
      default: false,
    },
    labelPosition: {
      type: String,
      default: 'inside',
      validator: (value: string) => [
        '',
        'inside',
        'on-border'
      ].indexOf(value) > -1
    },
    multiple: {
      type: Boolean,
      default: false
    },
    showLabel: {
      type: Boolean,
      default: true
    },
    withDragAndDrop: {
      type: Boolean,
      default: true
    },
    withButton: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    file() {
      if (this.file.size <= 102400) {
        this.errors.uploadError = ''
        this.$emit('uploaded', this.file)
      } else {
        this.errors.uploadError = 'The file is too large. Please try a smaller file.'
      }
    }
  },
  data() {
    return {
      file: {} as File,
      errors: {
        uploadError: ''
      }
    }
  }
})
